<template>
    <div id="create">
        {{proInfo}}
        <input value="这里是标题" class="h2">
        <hr>
        <!--显示和编辑题目-->
        <div class="detail" >
           <!--<radio :title="item.title" :options="item.options" :index="index"></radio>-->
            <div  v-for="(item, index) in proInfo">
                <div class="radio" v-if="item.type=='radio'">
                    {{index+1}}. <input  :value="item.title" class="text textArea">
                    <br>
                    <template v-for="option in item.options">
                        <div class="block">
                            <input type="radio"   :value="option"><input type="text" class="text textArea" :value="option">
                        </div>
                    </template>

                </div>
                <div class="checkbox" v-if="item.type=='checkbox'">
                    {{index+1}}. <input  :value="item.title" class="text textArea">
                    <br>
                    <template v-for="option in item.options">
                        <div class="block">
                            <input type="checkbox"   :value="option"><input type="text" class="text textArea" :value="option">
                        </div>
                    </template>

                </div>
                <div class="textType" v-if="item.type=='text'">
                    {{index+1}}. <input  :value="item.title" class="text textArea">
                    <br>
                    <textarea></textarea>

                </div>
                <div class="right">
                    <button>复用</button>
                    <button>删除</button>
                    <button>上移</button>
                    <button>下移</button>
                </div>

            </div>
            <!--<radio  v-for="item in proInfo" :item="item"  ></radio>-->
        </div>
        <div class="addQuestion">
            <div class="row" v-show="addShow">
                <button @click="createRadio">单选</button>
                <button @click="createCheckbox">多选</button>
                <button @click="createText">文本题</button>
            </div>
            <button class="text" @click="showType">+ 添加问题</button>
        </div>
        <hr>
        <div class="option">
            <div class="inline_block">
                <label for="dead_time">问卷截止日期</label>
                <input id="dead_time" type="date">
            </div>
            <div class="inline_block">
                <button type="button">保存问卷</button>
                <button type="button">发布问卷</button>
            </div>

        </div>
    </div>
</template>

<script>
//    import Vue from 'vue'
    import radio from  '../components/radio.vue';
    //使用module.exports这样的样式
    module.exports = {

            data : function(){
                return{
                    addShow : false,
                    proInfo : [{
                        type:'radio',
                        title:'sss',
                        options:['ee','rr']
                    }],
                }
            },
            methods:{
                showType : function () {
                    this.addShow = !this.addShow;

                },
                createRadio : function () {
                    var item = {};
                    item.type = 'radio',
                    item.title = '题目',
                    item.options=['选项1']
                    this.proInfo.push(item);
                },
                createCheckbox : function () {
                    var item = {};
                    item.type = 'checkbox',
                    item.title = '题目',
                    item.options=['选项1']
                    this.proInfo.push(item);
                },
                createText : function () {
                    var item = {};
                    item.type = 'text',
                    item.title = '题目',
                    this.proInfo.push(item);
                }
            },
        components : {
                'radio' : {
                    template : radio,
                    props : ['item'],
                    }
                }


    }



</script>

<style>
.addQuestion{
    width: 80%;
    min-height: 100px;
    line-height: 100px;
    border: 1px solid #cccccc;
    text-align: center;
    font-size: 1.2em;
    margin: 20px auto;
}
.detail{
    width: 80%;
}
    .h2{
        width:100%;
        font-size: 1.5em;
        font-weight: bold;
        text-align: center;
        border: none;
        margin-bottom: 20px;
    }
    .option{
        margin-top: 20px;
        display: flex;
        justify-content: space-around;
    }
    .inline_block{
        display: inline;
    }
    .block{
        display: block;
    }
    .row{
        height:40px ;
    }
    .text{
        border: none;
        background-color: #ffffff;
        font-size: 1.0em;
    }
.textArea{
    width: 80%;
    min-height: 30px;
}
    .radio, .checkbox, .textType {
        margin-top: 20px;
    }
    .right {
       overflow: auto;
    }
    .right button{
        float: right;

    }
</style>


